<template>
  <div>
    <div class="product">
      <div class="product-image">
        <img :src="image">
      </div>
      <div class="product-info">
        <h1>{{ title }}</h1>
        <p v-if="inStock > 10">In Stock</p>
        <p v-else-if="inStock > 0 && inStock<=10">Almost sold out!</p>
        <p v-else>Out of Stock</p>
        <ul>
          <li v-for="(detail, index) in goodInfo.details" :key="index">{{detail}}</li>
        </ul>
        <div v-for="(variant, index) in goodInfo.variants"
             :key="variant.variantId"
             class="color-box"
             :style="{ backgroundColor: variant.variantColor }"
             @mouseover="updateProduct(index)" >
        </div>
        <button @click="addToCart"
                :disabled="!inStock"
                :class="{ disabledButton: !inStock }" >Add to Cart</button>
        <div>
          <ProductTabs :reviews="goodInfo.reviews" :productId="goodInfo.productId"></ProductTabs>
        <p v-show="!premium">Появляется, когда ты красавчик</p>
      </div>
    </div>
  </div>
  </div>
</template>

<script>
  import ProductTabs from './ProductTabs.vue'

  export default {
    name: 'product',
    components: {
      ProductTabs
    },
    props: {
      goodInfo: {
        type: Object,
        required: true,
        validator: function (value) {
          if (
                  typeof value.productId !== 'number' ||
                  typeof value.brand !== 'string' ||
                  typeof value.details !== 'object' ||
                  !value.details.length ||
                  typeof value.details[0] !=='string' ||
                  typeof value.premium !=='boolean' ||
                  typeof value.product !=='string' ||
                  typeof value.reviews !=='object' ||
                  typeof value.selectedVariant !=='number' ||
                  typeof value.variants !=='object' ||
                  !value.variants.length ||
                  typeof value.variants[0] !=='object' ||
                  typeof value.variants[0].variantColor !=='string' ||
                  typeof value.variants[0].variantId !=='number' ||
                  typeof value.variants[0].variantImage !=='string' ||
                  typeof value.variants[0].variantQuantity !=='number'
          ) {
              return false
            }
            return true;
        }
      }
    },
    data: function () {
      return {

      }
    },
    methods:{
      addToCart() {
        this.$emit('add-to-cart', this.goodInfo.variants[this.goodInfo.selectedVariant].variantId)
      },
      updateProduct(index) {
        this.goodInfo.selectedVariant = index;
      }
    },
    mounted() {
      this.$eventBus.$on('review-submitted', productReview => {
          if (productReview.productId === this.goodInfo.productId) {
            this.goodInfo.reviews.push(productReview)
          }
      })
    },
    computed: {
      title() {
        return this.goodInfo.brand +' ' + this.goodInfo.product;
      },
      image() {
        return this.goodInfo.variants[this.goodInfo.selectedVariant].variantImage;
      },
      inStock() {
        return this.goodInfo.variants[this.goodInfo.selectedVariant].variantQuantity;
      },
      premium() {
        return this.goodInfo.premium;
      }
    }
  }
</script>
